import React from 'react';
import Select from 'react-select';
import { navigate } from "gatsby"

const options = [
  { value: 'shanghai-hongqiao-international-airport', label: ' - Shanghai Hongqiao International Airport' },
  { value: 'xtd-plaza', label: ' - XTD Plaza' },
  { value: 'sinopec-jinxiu-road', label: '- Sinopec, Jinxiu Road' },
];

class Form extends React.Component {
  state = {
    selectedOption: null,
  };
  handleChange = selectedOption => {
    this.setState({ selectedOption });
    navigate(`/locations/${selectedOption.value}/`)
  };

  render() {
    const { selectedOption } = this.state;

    return (
      <Select
        value={selectedOption}
        placeholder={'Select a location'}
        onChange={this.handleChange}
        options={options}
        clearable={false}
      />
    );
  }
}

export default Form
